import React from "react";
import {
  DermaSlideContainer,
  CategoryBar,
  TitleCategory,
  Title,
  MainContent,
  Subtitle,
  ImagesWrapper,
  StyledImg,
} from "./styles";
import Derma_slide_img from "../../../assets/derma_slide_img.jpg";

export default function DermaSlide() {
  return (
    <DermaSlideContainer>
      <CategoryBar>
        <TitleCategory>
          <Title>DermaSlide</Title>
        </TitleCategory>
      </CategoryBar>
      <MainContent>
        <Subtitle>
                   ,    -    .   advanced-      ,   ,   ,      .          ,       .    ,              .        ,     .      ,          .    ,       .
        </Subtitle>
        <ImagesWrapper>
          <StyledImg src={Derma_slide_img} alt="DermaSlide screenshot" />
        </ImagesWrapper>
      </MainContent>
    </DermaSlideContainer>
  );
}
