import React from "react";
import {
  ProductsContainer,
  HeroSection,
  HeroContent,
  MainTitle,
  MainSubtitle,
  ProductsGrid,
  ProductCard,
  ProductHeader,
  ProductName,
  ProductAccent,
  ProductDescription,
  ProductText,
  ProductActions,
  MoreButton,
  FloatingElements,
} from "./styles";

const productsData = [
  {
    id: "dentalpath",
    name: "DentalPath",
    description: "    .      -.",
    link: "/dentalPath",
    delay: "0s"
  },
  {
    id: "dermaslide", 
    name: "DermaSlide",
    description: "    .          .",
    link: "/dermaSlide",
    delay: "0.1s"
  },
  {
    id: "breastcancer",
    name: "BreastCancer", 
    description: "      .",
    link: "/breastCr",
    delay: "0.2s"
  },
  {
    id: "covid19",
    name: "Covid-19",
    description: "       .",
    link: "/covid-19", 
    delay: "0.3s"
  }
];

export default function Products() {
  return (
    <ProductsContainer>
      <FloatingElements />
      
      <HeroSection>
        <HeroContent>
          <MainTitle></MainTitle>
          <MainSubtitle>
                  
          </MainSubtitle>
        </HeroContent>
      </HeroSection>

      <ProductsGrid>
        {productsData.map((product, index) => (
          <ProductCard 
            key={product.id}
            style={{ 
              animationDelay: product.delay,
            }}
          >
            <ProductHeader>
              <ProductName className="product-name">{product.name}</ProductName>
              <ProductAccent />
            </ProductHeader>
            
            <ProductDescription>
              <ProductText>{product.description}</ProductText>
            </ProductDescription>

            <ProductActions>
              <MoreButton to={product.link} className="more-button">
                
              </MoreButton>
            </ProductActions>
          </ProductCard>
        ))}
      </ProductsGrid>
    </ProductsContainer>
  );
}
