import React from "react";
import {
  ProjectsContainer,
  CategoryBar,
  TitleCategory,
  Title,
  ProjectsGrid,
  ProjectCard,
  ProjectHeader,
  ProjectName,
  ProjectLine,
  ProjectSubtitleContainer,
  ProjectSubtitle,
  MoreButton,
} from "./styles";

export default function Projects() {
  return (
    <ProjectsContainer>
      <CategoryBar>
        <TitleCategory>
          <Title> </Title>
        </TitleCategory>
      </CategoryBar>

      <ProjectsGrid>
        <ProjectCard>
          <ProjectHeader>
            <ProjectName>Path AI</ProjectName>
            <ProjectLine />
          </ProjectHeader>
          <ProjectSubtitleContainer>
            <ProjectSubtitle>
                     .
              <MoreButton to="/pathAI"></MoreButton>
            </ProjectSubtitle>
          </ProjectSubtitleContainer>
        </ProjectCard>

        <ProjectCard>
          <ProjectHeader>
            <ProjectName>Hist Arch</ProjectName>
            <ProjectLine />
          </ProjectHeader>
          <ProjectSubtitleContainer>
            <ProjectSubtitle>
                     .
              <MoreButton to="/histArch"></MoreButton>
            </ProjectSubtitle>
          </ProjectSubtitleContainer>
        </ProjectCard>

        <ProjectCard>
          <ProjectHeader>
            <ProjectName>Doc AI</ProjectName>
            <ProjectLine />
          </ProjectHeader>
          <ProjectSubtitleContainer>
            <ProjectSubtitle>
              AI        .
              <MoreButton to="/docAI"></MoreButton>
            </ProjectSubtitle>
          </ProjectSubtitleContainer>
        </ProjectCard>
      </ProjectsGrid>
    </ProjectsContainer>
  );
}
