import React from "react";
import { useNavigate } from "react-router-dom";
import {
  Box,
  Typography,
  Button,
  TextField,
  Grid,
  Paper,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Divider,
} from "@mui/material";
import {
  Save,
  Close,
  Person,
  CalendarToday,
  Biotech,
} from "@mui/icons-material";

const NewCasePage = () => {
  const navigate = useNavigate();

  return (
    <Box
      sx={{
        minHeight: "100vh",
        background: "linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%)",
        pt: 12,
        pb: 4,
        px: { xs: 2, md: 4 },
      }}
    >
      {/* Header & Actions */}
      <Box
        sx={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          mb: 4,
        }}
      >
        <Box>
          <Typography variant="h4" fontWeight="bold" color="text.primary">
              
          </Typography>
          <Typography variant="body2" color="text.secondary">
               (Minsk Protocol)
          </Typography>
        </Box>
        <Box sx={{ display: "flex", gap: 2 }}>
          <Button
            variant="outlined"
            color="inherit"
            startIcon={<Close />}
            onClick={() => navigate("/archive")}
            sx={{ bgcolor: "white" }}
          >
            
          </Button>
          <Button
            variant="contained"
            color="primary"
            startIcon={<Save />}
            sx={{ boxShadow: 2 }}
          >
            
          </Button>
        </Box>
      </Box>

      <Grid container spacing={3}>
        {/* SOL KOLON */}
        <Grid item xs={12} lg={4}>
          <Box sx={{ display: "flex", flexDirection: "column", gap: 3 }}>
            {/* Kart 1 */}
            <Paper
              sx={{
                p: 3,
                borderRadius: 3,
                bgcolor: "rgba(255,255,255,0.8)",
                backdropFilter: "blur(10px)",
              }}
            >
              <Box
                sx={{ display: "flex", alignItems: "center", gap: 1, mb: 2 }}
              >
                <Person color="primary" />
                <Typography
                  variant="subtitle1"
                  fontWeight="bold"
                  color="primary.main"
                >
                    
                </Typography>
              </Box>

              <Box sx={{ display: "flex", flexDirection: "column", gap: 2 }}>
                <TextField
                  label=" "
                  placeholder=":  . ."
                  fullWidth
                  size="small"
                />
                <Box sx={{ display: "flex", gap: 2 }}>
                  <TextField
                    label=" "
                    placeholder="7087765A..."
                    fullWidth
                    size="small"
                  />
                  <TextField
                    type="date"
                    label=" "
                    fullWidth
                    size="small"
                    InputLabelProps={{ shrink: true }}
                  />
                </Box>
                <FormControl fullWidth size="small">
                  <InputLabel></InputLabel>
                  <Select label="" defaultValue="male">
                    <MenuItem value="male"></MenuItem>
                    <MenuItem value="female"></MenuItem>
                  </Select>
                </FormControl>
                <TextField
                  label=" "
                  multiline
                  rows={2}
                  fullWidth
                  size="small"
                />
              </Box>
            </Paper>

            {/* Kart 2 */}
            <Paper
              sx={{
                p: 3,
                borderRadius: 3,
                bgcolor: "rgba(255,255,255,0.8)",
                backdropFilter: "blur(10px)",
              }}
            >
              <Box
                sx={{ display: "flex", alignItems: "center", gap: 1, mb: 2 }}
              >
                <CalendarToday color="primary" />
                <Typography
                  variant="subtitle1"
                  fontWeight="bold"
                  color="primary.main"
                >
                   
                </Typography>
              </Box>

              <Box sx={{ display: "flex", flexDirection: "column", gap: 2 }}>
                <Box sx={{ display: "flex", gap: 2 }}>
                  <TextField
                    type="date"
                    label=" "
                    fullWidth
                    size="small"
                    InputLabelProps={{ shrink: true }}
                  />
                  <TextField
                    type="datetime-local"
                    label=" "
                    fullWidth
                    size="small"
                    InputLabelProps={{ shrink: true }}
                  />
                </Box>
                <FormControl fullWidth size="small">
                  <InputLabel> </InputLabel>
                  <Select label=" " defaultValue="ops">
                    <MenuItem value="ops"> </MenuItem>
                    <MenuItem value="fna">  (FNA)</MenuItem>
                    <MenuItem value="exc"> </MenuItem>
                  </Select>
                </FormControl>
                <Box sx={{ display: "flex", gap: 2 }}>
                  <TextField
                    type="datetime-local"
                    label=" "
                    fullWidth
                    size="small"
                    InputLabelProps={{ shrink: true }}
                  />
                  <TextField
                    type="number"
                    label="- "
                    defaultValue={1}
                    fullWidth
                    size="small"
                  />
                </Box>
              </Box>
            </Paper>
          </Box>
        </Grid>

        {/* SA KOLON */}
        <Grid item xs={12} lg={8}>
          <Paper
            sx={{
              p: 4,
              height: "100%",
              borderRadius: 3,
              bgcolor: "rgba(255,255,255,0.9)",
            }}
          >
            <Box sx={{ display: "flex", alignItems: "center", gap: 1, mb: 4 }}>
              <Biotech color="primary" fontSize="large" />
              <Typography variant="h6" fontWeight="bold" color="primary.main">
                 
              </Typography>
            </Box>

            <Box sx={{ display: "flex", flexDirection: "column", gap: 4 }}>
              <Box>
                <Typography
                  variant="subtitle2"
                  fontWeight="bold"
                  sx={{ mb: 1 }}
                >
                  14.  
                </Typography>
                <TextField
                  fullWidth
                  multiline
                  rows={4}
                  placeholder=": 5    ,  ..."
                  sx={{ bgcolor: "#f9fafb" }}
                />
              </Box>

              <Box>
                <Typography
                  variant="subtitle2"
                  fontWeight="bold"
                  sx={{ mb: 1 }}
                >
                  15.  
                </Typography>
                <TextField
                  fullWidth
                  multiline
                  rows={6}
                  placeholder="  ..."
                  sx={{ bgcolor: "#f9fafb" }}
                />
              </Box>

              <Box
                sx={{
                  bgcolor: "#e3f2fd",
                  p: 3,
                  borderRadius: 2,
                  border: "1px solid #90caf9",
                }}
              >
                <Typography
                  variant="subtitle2"
                  fontWeight="bold"
                  color="primary.dark"
                  sx={{ mb: 1 }}
                >
                  16.  
                </Typography>
                <TextField
                  fullWidth
                  multiline
                  rows={3}
                  placeholder=": ..."
                  variant="standard"
                  InputProps={{ disableUnderline: true }}
                />
              </Box>
            </Box>

            <Divider sx={{ my: 4 }} />

            <Box
              sx={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "flex-end",
              }}
            >
              <Box>
                <Typography variant="caption" color="text.secondary">
                   
                </Typography>
                <Typography variant="body1" fontWeight="medium">
                  --.--.2025
                </Typography>
              </Box>
              <Box sx={{ textAlign: "right" }}>
                <Typography variant="caption" color="text.secondary">
                  -
                </Typography>
                <Typography
                  variant="h6"
                  fontFamily="serif"
                  sx={{ fontStyle: "italic", color: "#1e3a8a" }}
                >
                  -  . .
                </Typography>
              </Box>
            </Box>
          </Paper>
        </Grid>
      </Grid>
    </Box>
  );
};

export default NewCasePage;
