import React, { useRef } from "react";
import { useNavigate } from "react-router-dom";

import {
  MainPageContainer,
  HeroSection,
  HeroContent,
  ScrollButton,
  ContentSection,
  MainTitle,
  MainSubitle,
  SectionDivider,
  ProductName,
  Line,
  TestCardsGrid,
  TestCard,
  TestTitle,
  FeatureSection,
  FeatureGrid,
  FeatureImage,
  CompContainer,
  SubCompContainer,
  SubCompTitle,
  SubCompSubtitle,
  StatisticWrapper,
  StatTitle,
  StatisticsContainer,
  StatCard,
  Amount,
  StatSubitle,
  MainFooterContainer, // Bunun styles.ts iinde olduundan emin ol
} from "./styles";

import page4 from "../../assets/mainPage4.jpg";
import page5 from "../../assets/mainPage5.jpg";
import page6 from "../../assets/covid19.jpg";
import Comp from "../../assets/Comp.png";

// SADECE FOOTER IMPORT EDYORUZ
import Footer from "../Footer";

export default function Main() {
  const navigate = useNavigate();
  const detailsRef = useRef<HTMLDivElement>(null);

  const scrollToDetails = () => {
    detailsRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  const testData = [
    { image: page4, title: "  " },
    { image: page5, title: "  " },
    { image: page6, title: "Covid-19" },
  ];

  const features = [
    { title: " ", subtitle: "   ." },
    {
      title: " ",
      subtitle: "   .",
    },
    {
      title: "",
      subtitle: " AI-   .",
    },
  ];

  const statistics = [
    { amount: "10+", subtitle: "  AI" },
    { amount: "105+", subtitle: "  " },
    { amount: "5+", subtitle: "   " },
  ];

  return (
    <MainPageContainer>
      <HeroSection>
        <HeroContent>
          <h1>
            CALADRIUS <br />  
          </h1>
          <p>     .</p>
          <ScrollButton onClick={scrollToDetails}>  </ScrollButton>
        </HeroContent>
      </HeroSection>

      <div ref={detailsRef}>
        <ContentSection>
          <MainTitle> </MainTitle>
          <MainSubitle>   .</MainSubitle>
          <SectionDivider>
            <Line />
            <ProductName></ProductName>
            <Line />
          </SectionDivider>
        </ContentSection>
      </div>

      <TestCardsGrid>
        {testData.map((item, i) => (
          <TestCard key={i} onClick={() => navigate("/projects")}>
            <TestTitle>{item.title}</TestTitle>
            <img src={item.image} alt={item.title} />
          </TestCard>
        ))}
      </TestCardsGrid>

      <FeatureSection>
        <FeatureGrid>
          <FeatureImage>
            <img src={Comp} alt="Interface" />
          </FeatureImage>
          <CompContainer>
            {features.map((f, i) => (
              <SubCompContainer key={i}>
                <SubCompTitle>{f.title}</SubCompTitle>
                <SubCompSubtitle>{f.subtitle}</SubCompSubtitle>
              </SubCompContainer>
            ))}
          </CompContainer>
        </FeatureGrid>
      </FeatureSection>

      <StatisticWrapper>
        <StatTitle> </StatTitle>
        <StatisticsContainer>
          {statistics.map((s, i) => (
            <StatCard key={i}>
              <Amount>{s.amount}</Amount>
              <StatSubitle>{s.subtitle}</StatSubitle>
            </StatCard>
          ))}
        </StatisticsContainer>
      </StatisticWrapper>

      {/* FOOTER: Partnerler ve Aha7 iinde */}
      <MainFooterContainer>
        <Footer />
      </MainFooterContainer>
    </MainPageContainer>
  );
}
