import React from "react";
import {
  HistArchContainer,
  CategoryBar,
  TitleCategory,
  Title,
  MainContent,
  Subtitle,
  ImageWrapper,
  StyledImg,
} from "./styles";
import HistArch_img from "../../../assets/HistArch.png";

export default function HistArch() {
  return (
    <HistArchContainer>
      <CategoryBar>
        <TitleCategory>
          <Title>HistArch</Title>
        </TitleCategory>
      </CategoryBar>
      <MainContent>
        <Subtitle>
              ,
               
          .     
             .  
               , 
           .
        </Subtitle>
        <ImageWrapper>
          <StyledImg src={HistArch_img} alt="HistArch screenshot"/>
        </ImageWrapper>
      </MainContent>
    </HistArchContainer>
  );
}
