import React from "react";
import {
  FooterContainer,
  PartnersRow,
  FooterPartnerLogo,
  ProjectInfoContainer,
  ProjectInfoImage,
  FooterContent,
  FooterColumn,
  FooterLogo,
  FooterDescription,
  ColumnTitle,
  FooterLinks,
  FooterLink,
  ContactItem,
  SocialIcons,
  SocialIcon,
  Copyright,
} from "./styles";

import Logo from "../../assets/logo.png";
import {
  Phone,
  Email,
  LocationOn,
  Telegram,
  LinkedIn,
} from "@mui/icons-material";

// PARTNERLER
import Aha1 from "../../assets/aha1.jpg";
import Aha2 from "../../assets/aha2.jpg";
import Aha3 from "../../assets/aha3.jpg";
import Aha4 from "../../assets/aha4.png";
import Aha5 from "../../assets/aha5.jpg";
import Aha6 from "../../assets/aha6.png";

// AHA7 (Destek Grseli)
import Aha7 from "../../assets/aha7.jpg";

const partners = [Aha1, Aha2, Aha3, Aha4, Aha5, Aha6];

export default function Footer() {
  return (
    <FooterContainer>
      {/* 1. PARTNER LOGOLARI */}
      <PartnersRow>
        {partners.map((img, index) => (
          <FooterPartnerLogo
            key={index}
            src={img}
            alt={`Partner ${index + 1}`}
          />
        ))}
      </PartnersRow>

      {/* 2. PROJE BLG GRSEL */}
      <ProjectInfoContainer>
        <ProjectInfoImage src={Aha7} alt="   " />
      </ProjectInfoContainer>

      {/* 3. FOOTER ER (RUSA) */}
      <FooterContent>
        <FooterColumn>
          <FooterLogo>
            <img src={Logo} alt="Caladrius Logo" />
            CALADRIUS
          </FooterLogo>
          <FooterDescription>
                  .
              ,  .
          </FooterDescription>
        </FooterColumn>

        <FooterColumn>
          <ColumnTitle></ColumnTitle>
          <FooterLinks>
            <FooterLink to="/"></FooterLink>
            <FooterLink to="/products"></FooterLink>
            <FooterLink to="/projects"></FooterLink>
            <FooterLink to="/about"> </FooterLink>
          </FooterLinks>
        </FooterColumn>

        <FooterColumn>
          <ColumnTitle></ColumnTitle>
          <FooterLinks>
            <FooterLink to="/contacts"></FooterLink>
            <FooterLink to="/privacy"> </FooterLink>
            <FooterLink to="/terms"> </FooterLink>
            <FooterLink to="/faq">FAQ</FooterLink>
          </FooterLinks>
        </FooterColumn>

        <FooterColumn>
          <ColumnTitle>  </ColumnTitle>
          <ContactItem>
            <Phone fontSize="small" />
            <span>8 800 555-92-67</span>
          </ContactItem>
          <ContactItem>
            <Email fontSize="small" />
            <span>caladrius.technology@gmail.com</span>
          </ContactItem>
          <ContactItem>
            <LocationOn fontSize="small" />
            <span>Minsk, Belarus</span>
          </ContactItem>

          <SocialIcons>
            <SocialIcon href="#" target="_blank">
              <Telegram />
            </SocialIcon>
            <SocialIcon href="#" target="_blank">
              <LinkedIn />
            </SocialIcon>
          </SocialIcons>
        </FooterColumn>
      </FooterContent>

      <Copyright>
         {new Date().getFullYear()} CALADRIUS Technology.   .
      </Copyright>
    </FooterContainer>
  );
}
