// src/Contexts/AuthContext.tsx

import React, { createContext, useContext, useState, useEffect } from "react";
import { User, AuthContextType } from "../types/User";

// --- TEST KULLANICILARI (MOCK DATA) ---
// Bu kullanc bilgileriyle giri yaparak farkl ekranlar test edebilirsin.
const mockUsers: Record<string, { password: string; user: User }> = {
  "student@test.com": {
    password: "123456",
    user: {
      id: "1",
      email: "student@test.com",
      firstName: "", // simler Rusa kalabilir veya deitirebilirsin
      lastName: "",
      role: "student",
      institution: "  ",
      course: "4 ",
      group: " 2",
      faculty: " ",
    },
  },
  "teacher@test.com": {
    password: "123456",
    user: {
      id: "2",
      email: "teacher@test.com",
      firstName: ". ",
      lastName: "",
      role: "teacher",
      institution: "  ",
      department: " ",
      position: "",
    },
  },
  "doc@test.com": {
    password: "123456",
    user: {
      id: "3",
      email: "doc@test.com",
      firstName: "- ",
      lastName: "",
      role: "doc",
      institution: "  1",
      department: " ",
      position: "-",
    },
  },
};

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const AuthProvider: React.FC<{ children: React.ReactNode }> = ({
  children,
}) => {
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true); // Ykleniyor durumu eklendi

  useEffect(() => {
    // Sayfa yenilendiinde kullancy hatrla
    const savedUser = localStorage.getItem("currentUser");
    if (savedUser) {
      try {
        setUser(JSON.parse(savedUser));
      } catch (e) {
        console.error("Kullanc verisi okunamad", e);
      }
    }
    setLoading(false);
  }, []);

  const login = async (email: string, password: string): Promise<boolean> => {
    // Gerek API yerine mockUsers listesinden kontrol ediyoruz
    const userCredentials = mockUsers[email];

    if (userCredentials && userCredentials.password === password) {
      setUser(userCredentials.user);
      localStorage.setItem("currentUser", JSON.stringify(userCredentials.user));
      return true;
    }

    return false;
  };

  const logout = () => {
    setUser(null);
    localStorage.removeItem("currentUser");
  };

  const value = {
    user,
    login,
    logout,
    isAuthenticated: !!user,
    loading,
  };

  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error("useAuth must be used within AuthProvider");
  }
  return context;
};
