import React from "react";
import {
  DentalPathContainer,
  CategoryBar,
  TitleCategory,
  Title,
  MainContent,
  Subtitle,
  ImagesWrapper,
  StyledImg,
} from "./styles";
import DentalPath_img from "../../../assets/dental_path_img.jpg";

export default function DentalPath() {
  return (
    <DentalPathContainer>
      <CategoryBar>
        <TitleCategory>
          <Title>DentalPath</Title>
        </TitleCategory>
      </CategoryBar>
      <MainContent>
        <Subtitle>
                    ,     -.         ,   ,         .         ,    ,        .         .      ,             .        ,       .
        </Subtitle>
        <ImagesWrapper>
          <StyledImg src={DentalPath_img} alt="Dental Path screenshot" />
        </ImagesWrapper>
      </MainContent>
    </DentalPathContainer>
  );
}
