import React from "react";
import { useAuth } from "../../Contexts/AuthContext"; // AuthContext eklendi
import { Avatar } from "@mui/material"; // Avatar iin
import {
  HeaderContainer,
  HeaderInner,
  TopBar,
  ContactItem,
  MainNav,
  LogoBrand,
  NavigationMenu,
  NavItem,
  ActionSection,
  SocialLinks,
  TelegramIcon,
  VKIcon,
  LoginButton,
  MobileMenuButton,
  UserProfileLink, // Yeni eklenen profil linki stili
} from "./styles";

import Logo from "../../assets/logo.png";
import PhoneIcon from "../../assets/PhoneIcon.png";
import MailIcon from "../../assets/Mailicon.png";
import Burger from "./Burger";

function Header() {
  const { user } = useAuth(); // Kullanc bilgisini ek

  return (
    <HeaderContainer>
      <HeaderInner>
        {/* st Bilgi ubuu */}
        <TopBar>
          <ContactItem href="tel:+78005559267">
            <img src={PhoneIcon} alt="phone" />
            <span>8 800 555-92-67</span>
          </ContactItem>
          <ContactItem href="mailto:caladrius.technology@gmail.com">
            <img src={MailIcon} alt="email" />
            <span>caladrius.technology@gmail.com</span>
          </ContactItem>
        </TopBar>

        {/* Ana Navigasyon */}
        <MainNav>
          {/* Logo */}
          <LogoBrand to="/">
            <img src={Logo} alt="CALADRIUS" />
            <span className="brand-text">CALADRIUS</span>
          </LogoBrand>

          {/* Men Linkleri */}
          <NavigationMenu>
            <NavItem to="/"></NavItem>
            <NavItem to="/products"></NavItem>
            <NavItem to="/projects"></NavItem>
            <NavItem to="/archive"></NavItem>
            <NavItem to="/about"> </NavItem>
            <NavItem to="/contacts"></NavItem>

            {/* Kullanc giri yapmsa Canvas linkini de gster */}
            {user && (
              <NavItem to="/canvas" style={{ color: "#2563eb" }}>
                 
              </NavItem>
            )}
          </NavigationMenu>

          {/* Sa Aksiyonlar */}
          <ActionSection>
            <SocialLinks>
              <TelegramIcon as="a" href="https://t.me/" target="_blank" />
              <VKIcon as="a" href="https://vk.com/" target="_blank" />
            </SocialLinks>

            {/* DNAMK GR BUTONU */}
            {user ? (
              // Kullanc Varsa: Profil Linki
              <UserProfileLink to="/dashboard">
                <Avatar
                  sx={{
                    width: 32,
                    height: 32,
                    bgcolor: "#2563eb",
                    fontSize: 14,
                  }}
                  src={user.avatar}
                >
                  {user.firstName ? user.firstName[0] : "U"}
                </Avatar>
                <span>{user.firstName}</span>
              </UserProfileLink>
            ) : (
              // Kullanc Yoksa: Giri Butonu
              <LoginButton to="/auth"></LoginButton>
            )}

            <MobileMenuButton>
              <Burger />
            </MobileMenuButton>
          </ActionSection>
        </MainNav>
      </HeaderInner>
    </HeaderContainer>
  );
}

export default Header;
